import React, { useEffect } from "react";
import { Icon } from "@iconify/react";
import { useDispatch, useSelector } from "react-redux";
import { CardTitle, Table } from "reactstrap";
import {

  adminfetchTimeOff,
  createAdminTimesoffSuccess,
} from "../action/action";
import { Link } from "react-router-dom";
import { useState } from "react";
import { DatePicker } from "antd";
import {
  Button as BootstrapButton,
  Modal,
  ModalHeader,
  ModalBody,
  ModalFooter,
} from "reactstrap";
const Buttons = () => {
  const [isOpen, setIsOpen] = useState(false);
  const [requestType, setRequestType] = useState("");
  const [startDate, setStartDate] = useState(null);
  const [endDate, setEndDate] = useState(null);
  const [description, setDescription] = useState("");
  const [attachment, setAttachment] = useState(null);
  const [filteredData, setFilteredData] = useState([]);
  const [leavetype , setLeaveType] = useState("")

  // State variables to keep track of the selected start and end dates
  const [selectedStartDate, setSelectedStartDate] = useState(null);
  const [selectedEndDate, setSelectedEndDate] = useState(null);
  const dispatch = useDispatch();
  const timeOffData = useSelector((state) => state.timeOff.admintimeOff  );
console.log(timeOffData)
  const check = useSelector((state) => state);
  console.log(check);
  console.log("Redux State:", timeOffData);
  useEffect(() => {
    dispatch(adminfetchTimeOff());
  }, []);
  const filterDataByDateRange = () => {
    const filtered = timeOffData.filter((item) => {
      const fromDate = new Date(item.fromDate);
      const toDate = new Date(item.toDate);
      return (
        (selectedStartDate === null || fromDate >= selectedStartDate) &&
        (selectedEndDate === null || toDate <= selectedEndDate)
      );
    });
    setFilteredData(filtered);
  };
  useEffect(() => {
    filterDataByDateRange();
  }, [selectedStartDate, selectedEndDate, timeOffData]);
  
  // if (!timeOffData || timeOffData.length === 0) {
  //   return <div>Loading...</div>;
  // }

  const toggleModal = () => {
    setIsOpen(!isOpen);
  };

  // Function to handle form submission
  const handleSubmit = () => {
   const payload = {
    ReceiverEmail : requestType,
    FromDate:   startDate,
    ToDate: endDate,
    Attachment : attachment,
    Name: leavetype,
    Note: description,
    };
    dispatch(createAdminTimesoffSuccess(payload));
    console.log("fucntion called");
    toggleModal();
  };
  const handleUploadClick = () => {
    // Programmatically trigger the file input click event
    const fileInput = document.getElementById("attachmentInput");
    if (fileInput) {
      fileInput.click();
    }
  };
  return (
    <>
      <div className="header">
        <div className="left-corner">
          <i className="user-icon bi bi-airplane"></i>
          <div>Time Off</div>
        </div>
        <div className="tabs">
          <div className="tabs-bar">
            <div className="tabs-nav-container">
              <span className="tabs-tab-prev">
                <span className="tabs-tab-prev-icon">
                  <i className="tabs-tab-prev-icon-target"></i>
                </span>
              </span>
              <span className="tabs-tab-next">
                <span className="tabs-tab-next-icon">
                  <i className="tabs-tab-next-icon-target"></i>
                </span>
              </span>
              <div className="tabs-nav-wrap">
                <div className="tabs-nav-scroll">
                  <div className="tabs-nav">
                    <div className="tabs-tab">
                      <div className="hidden-div">
                        <Link className="anchor"
                          style={{
                            lineHeight: "55px",
                            display: "inline-block",
                          }}
                        >
                          MY Time Off
                        </Link>
                      </div>
                    </div>
                    <div className="tabs-tab">
                      <div className="hidden-div">
                        <Link
                          to="/adminteamoff"
                          style={{
                            lineHeight: "55px",
                            display: "inline-block",
                          }}
                        >
                          TEAM Time Off
                        </Link>
                      </div>
                    </div>
                    <div className="tabs-tab">
                      <div className="hidden-div">
                        <Link
                          to="/employeetimeoff"
                          style={{
                            lineHeight: "55px",
                            display: "inline-block",
                          }}
                        >
                          Employee Time Off
                        </Link>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div className="mytimeoff" style={{ marginTop: "120px" }}>
        <div className="insidemytime">
          <div>
            <h6>Unpaid</h6>
            <span>30 days</span>
          </div>
        </div>
      </div>

      <div className="myattedance">
        <div>
          <CardTitle tag="h6" className="border-bottom p-4 mt-0">
            <Icon
              icon="uil:file-edit-alt"
              color="#2ad25f"
              width={22}
              height={22}
            />
            <span style={{ marginLeft: "10px" }}>My Requests</span>
          </CardTitle>
        </div>
        <div className="filtercombinaion">
          <div className="datepicker">
            <DatePicker
            style={{width:"190px"}}
            value={selectedStartDate}
    onChange={(date) => setSelectedStartDate(date)}
            />
            <DatePicker  style={{width:"190px"}} value={selectedEndDate}
    onChange={(date) => setSelectedEndDate(date)} />
          </div>

          <button className="timeoffbuttton" onClick={toggleModal}>
            New Request
          </button>
        </div>
        <Table className="no-wrap mt-3 align-middle " responsive borderless>
          <thead>
            <tr>
              <th>From</th>
              <th>To</th>
              <th>Total</th>
              <th>Type</th>
              <th>Attachment</th>
              <th>Status</th>
            </tr>
          </thead>
          <tbody>
            {filteredData.map((tdata, index) => (
               
                  <tr key={index} className="border-top">
                    <td className="mb-0">{tdata.fromDate}</td>
                    <td>{tdata.toDate}</td>
                    <td>{tdata.totalTime}</td>
                    <td>{tdata.leaveTypeName}</td>
                    <td className="scrollable-td">{tdata.attachment}</td>
                    <td>
                      {tdata.statusName === "pending" ? (
                        <span className="p-2 bg-danger rounded-circle d-inline-block ms-3"></span>
                      ) : tdata.status === "holt" ? (
                        <span className="p-2 bg-warning rounded-circle d-inline-block ms-3"></span>
                      ) : (
                        <span className="p-2 bg-success rounded-circle d-inline-block ms-3"></span>
                      )}
                    </td>
                  </tr>
                  ))}
          </tbody>
        </Table>
      </div>
      {/* <div className="myattedance">
        <div>
          <CardTitle tag="h6" className="border-bottom p-4 mt-0">
            <Icon
              icon="uil:file-edit-alt"
              color="#2ad25f"
              width={22}
              height={22}
            />
            <span style={{ marginLeft: "10px" }}>Balance History</span>
          </CardTitle>
        </div>

        <Table className="no-wrap mt-3 align-middle " responsive borderless>
          <thead>
            <tr>
              <th>Date</th>
              <th>Event</th>
              <th>Type</th>
              <th>Changed By</th>
              <th>Changed (days)</th>
            </tr>
          </thead>
          <tbody>
            {timeOffData &&
              timeOffData.map((tdata, index) => (
                <tr key={index} className="border-top">
                  <td className="mb-0">{tdata.date}</td>

                  <td>{tdata.clockInTime}</td>
                  <td>{tdata.clockInLocation}</td>
                  <td>{tdata.clockOutTime}</td>
                  <td className="scrollable-td">{tdata.clockOutLocation}</td>
                </tr>
              ))}
          </tbody>
        </Table>
      </div> */}
      <Modal isOpen={isOpen} toggle={toggleModal} className="requestmodal">
  <ModalHeader toggle={toggleModal}>
    <i className="fas fa-plus-circle"></i> New Request
  </ModalHeader>
  <ModalBody className="requestbody">
    <div className="form-group">
      <div className="input-group">
        <div className="input-group-prepend">
          <span className="input-group-text">
          <Icon icon="uil:plane" />
          </span>
        </div>
        <input
          type="text"
          className="form-control"
          value={requestType}
          onChange={(e) => setRequestType(e.target.value)}
          placeholder="Type of Request"
        />
      </div>
    </div>
    <div className="form-group">
      <div className="input-group">
        <div className="input-group-prepend">
          <span className="input-group-text">
          <Icon icon="uil:clock" />
          </span>
        </div>
        <DatePicker.RangePicker
          style={{ width: "50%" }}
          value={[startDate, endDate]}
          onChange={(dates) => {
            setStartDate(dates[0]);
            setEndDate(dates[1]);
          }}
          placeholder="Date Range"
        />
      </div>
    </div>
    <div className="form-group">
      <div className="input-group">
        <div className="input-group-prepend">
          <span className="input-group-text">
          <Icon icon="uil:align-left" />
          </span>
        </div>
        <textarea
          className="form-control"
          value={description}
          onChange={(e) => setDescription(e.target.value)}
          placeholder="Description"
        ></textarea>
      </div>
    </div>
    <div className="form-group">
      <div className="input-group">
        <div className="input-group-prepend">
          <span className="input-group-text">
          <Icon icon="uil:paperclip" />
          </span>
        </div>
        <span  htmlFor="attachmentInput" onClick={() => handleUploadClick()} > Upload attachment</span>
        <input
         id="attachmentInput"
          type="file"
          style={{display: "none"}}
          className="form-control-file"
          onChange={(e) => setAttachment(e.target.files[0])}
        />
      </div>
    </div>
    <div className="form-group">
      <div className="input-group">
        <div className="input-group-prepend">
          <span className="input-group-text">
           <Icon icon="uil:bell" />
          </span>
        </div>
        <input
          type="text"
          className="form-control"
          value={leavetype}
          onChange={(e) => setLeaveType(e.target.value)}
          placeholder="Your Name"
        />
      </div>
    </div>
  </ModalBody>
  <ModalFooter>
    <BootstrapButton color="primary" onClick={handleSubmit}>
      Submit
    </BootstrapButton>{" "}
    <BootstrapButton color="secondary" onClick={toggleModal}>
      Cancel
    </BootstrapButton>
  </ModalFooter>
</Modal>

    </>
  );
};

export default Buttons;
